import { defineComponent, ref } from "vue";
import { Positions, MainStats, SubStats } from "@/stores/data";
const formRef = ref(null);
export default defineComponent({
  name: "ArtifactsForm",
  setup() {
    return {
      formRef,
      data: {
        positions: Positions,
        mainStats: MainStats,
        subStats: SubStats,
        lastSubStats: [{
          labelKey: 'artifacts.stats.none',
          icon: '',
          value: null
        }].concat(SubStats)
      }
    };
  },
  props: {
    changed: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      formValue: {
        position: 'flowerOfLife',
        mainStat: null,
        subStat1: null,
        subStat2: null,
        subStat3: null,
        subStat4: null
      }
    };
  },
  watch: {
    formValue: {
      deep: true,
      handler() {
        this.changed(this.formValue);
      }
    }
  }
});